.multisend-container {
  position: relative;
  z-index: 2;
  width: 960px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 980px) {
  .multisend-container {
    width: 96%;
  }
}
.header .multisend-container,
.form_header,
.form-inline,
.transactions-title {
  display: flex;
  justify-content: space-between;
}
