html,
body {
  color: #9cd8ff;
  font-size: 14px;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
}
html,
body,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
html {
  height: 100%;
}
body {
  position: relative;
  min-height: 100%;
  box-sizing: border-box;
  background-color: #011d2f;
}
body:after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  background-image: url(../images/light@2x.png);
  background-size: 100% 100%;
}
p {
  color: #fff;
  line-height: 22px;
  font-size: 14px;
}
a {
  color: #10b3ff;
}
a:hover {
  text-decoration: none;
}
strong {
  font-weight: bold;
}
.title {
  margin-bottom: 30px;
  color: #fff;
  line-height: 46px;
  font-size: 32px;
  font-weight: 300;
  box-sizing: content-box;
}
@media (max-width: 414px) {
  .title {
    font-size: 26px;
  }
}
.description {
  margin-bottom: 20px;
  color: #9cd8ff;
}
.multisend-container_bg:before {
  content: '';
  position: absolute;
  right: -120px;
  top: 10px;
  width: 570px;
  height: 445px;
  background-image: url(../images/illustration.png);
}
@media (min--moz-device-pixel-ratio: 1.3),
  (-o-min-device-pixel-ratio: 2.6 / 2),
  (-webkit-min-device-pixel-ratio: 1.3),
  (min-device-pixel-ratio: 1.3),
  (min-resolution: 1.3dppx) {
  .multisend-container_bg:before {
    background-image: url('../images/illustration@2x.png');
    background-size: 100% 100%;
  }
}
@media (max-width: 980px) {
  .multisend-container_bg:before {
    display: none;
  }
}
.multisend-container_opacity:before {
  opacity: 0.2;
}
.content {
  position: relative;
  z-index: 1;
  width: 50%;
}
.container_opacity .content {
  width: 100%;
}
@media (max-width: 980px) {
  .content {
    width: 100%;
  }
}
.break-all {
  word-break: break-all;
}
.header {
  margin-bottom: 40px;
  padding: 20px 0;
  border-bottom: 1px solid rgba(149, 213, 255, 0.1);
}
@media (max-width: 414px) {
  .header {
    margin-bottom: 30px;
  }
}
.header .multisend-container {
  align-items: center;
}
.header .multisend-label {
  display: block;
  margin-bottom: 20px;
  text-align: right;
  width: 100%;
}
.form_header .header .multisend-label {
  margin-right: 15px;
  margin-bottom: 0;
}
.header-logo {
  display: block;
  width: 253px;
  height: 36px;
  background-image: url(../images/logo.png);
}
@media (min--moz-device-pixel-ratio: 1.3),
  (-o-min-device-pixel-ratio: 2.6 / 2),
  (-webkit-min-device-pixel-ratio: 1.3),
  (min-device-pixel-ratio: 1.3),
  (min-resolution: 1.3dppx) {
  .header-logo {
    background-image: url('../images/logo.png');
    background-size: 100% 100%;
  }
}
@media (max-width: 980px) {
  .header-logo {
    width: 36px;
    background-size: 200px 100%;
  }
}
.input,
.select,
.multisend-textarea,
.multisend-textarea.form-control,
.multisend-button {
  outline: none;
  border-radius: 4px;
  line-height: 40px;
}
.header .multisend-label,
.input,
.select,
.multisend-textarea,
.multisend-textarea.form-control,
.multisend-label,
.multisend-button {
  color: #fff;
  font-size: 14px;
  font-family: 'Lato', sans-serif;
}
#addresses-with-balances {
  line-height: 20px;
}
#addresses-with-balances::-webkit-input-placeholder {
  color: #8e9ea9;
  opacity: 0.7;
}
#addresses-with-balances:-moz-placeholder {
  color: #8e9ea9;
  opacity: 0.7;
}
#addresses-with-balances::-moz-placeholder {
  color: #8e9ea9;
  opacity: 0.7;
}
#addresses-with-balances:-ms-input-placeholder {
  color: #8e9ea9;
  opacity: 0.7;
}
.form span.error {
  margin-left: 10px;
  color: #fd7491;
}
.form_header {
  align-items: center;
}
.form-group {
  width: 100%;
}
.form-inline {
  align-items: flex-start;
}
.form-inline-i_token-address {
  width: 100%;
  margin-bottom: 20px;
}
.form-inline-i_token-address-values {
  width: 100%;
}
.form-inline-i_gas-price {
  width: 100%;
  margin-bottom: 20px;
}
.form-inline-i_gas-sharing {
  width: 100%;
}
.form-inline-i_token-decimals {
  width: 15%;
}
.form-inline-i_balance {
  width: 40%;
}
.input,
.select,
.multisend-textarea {
  width: 100%;
  margin-bottom: 20px;
  transition: 0.3s box-shadow;
  box-sizing: border-box;
  border: 1px solid #95d5ff;
  background-color: rgba(255, 255, 255, 0.1);
}
.input:focus,
.select:focus,
.multisend-textarea:focus {
  box-shadow: 0px 10px 20px 0 rgba(16, 179, 255, 0.3);
}
.input,
.select {
  height: 40px;
}
.input {
  padding: 0 20px;
}
.input[type='number'] {
  padding-right: 0px;
}
.select {
  padding: 0 38px 0 20px;
  appearance: none;
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABAAAAAICAMAAADHqI+lAAAACVBMVEWT1P+T1P+T1P9um/dXAAAAAnRSTlMAgJsrThgAAAAlSURBVHgBVcixDQAgAMOwlv+PhqWWyBan56she+KJf+KJJ57sLy2wAIFnBVRPAAAAAElFTkSuQmCC);
  background-repeat: no-repeat;
  background-size: 8px 4px;
  background-position: center right 20px;
}
.form_header .select {
  max-width: 160px;
  margin-bottom: 0;
}
.multisend-textarea,
.multisend-textarea.form-control {
  width: 100%;
  margin-bottom: 20px;
  transition: 0.3s box-shadow;
  box-sizing: border-box;
  border: 1px solid #95d5ff;
  background-color: rgba(255, 255, 255, 0.1);
  resize: none;
  height: 186px;
  padding: 10px 10px;
  font-family: monospace;
}
.multisend-textarea:focus,
.multisend-textarea.form-control:focus {
  box-shadow: 0px 10px 20px 0 rgba(16, 179, 255, 0.3);
}
.multisend-label {
  display: block;
  margin-bottom: 20px;
}
.form_header .multisend-label {
  margin-right: 15px;
  margin-bottom: 0;
}
.multisend-button {
  cursor: pointer;
  display: inline-block;
  transition: 0.3s box-shadow;
  border: 0;
  padding: 0 20px;
  background-color: #10b3ff;
  background-repeat: no-repeat;
  text-decoration: none;
  font-size: 16px;
  font-weight: bold;
}
.multisend-button:disabled {
  -moz-box-shadow: inset 0 0 10px rgba(11, 72, 101, 0.3);
  -webkit-box-shadow: inset 0 0 10px rgba(11, 72, 101, 0.3);
  box-shadow: inset 0 0 10px rgba(11, 72, 101, 0.3);
  opacity: 0.88;
}
.multisend-button:disabled:hover {
  box-shadow: inset 0 0 10px rgba(11, 72, 101, 0.3);
}
.multisend-button:hover {
  box-shadow: 0px 10px 20px 0 rgba(16, 179, 255, 0.3);
}
